import React, { useEffect, useState } from 'react';
import './index.css';
import educationIcon from '../../assets/classroom.png';
import businessIcon from '../../assets/business.png';
import videoIcon from '../../assets/video.png';
import socialIcon from '../../assets/social.png';
import { Link } from "react-router-dom";
import { BaseUrl, Baseurl } from '../API'
import axios from 'axios';
import { useHistory } from 'react-router-dom';
import { set } from 'date-fns';

const Viewboard = () => {

  const [education ,setEducation] = useState([]);
  const history = useHistory();
  const redirect = (url) => {
    history.push(url)
  }
  useEffect(() => {
    getDetails();
  }, [])

  const getDetails = () => {
    const data = localStorage.getItem("user");
    const maindata = JSON.parse(data)
    axios.get(`${BaseUrl}/education/userdetails/${maindata._id}`).then(res => {
      setEducation(res.data)
        localStorage.setItem("education", JSON.stringify(res.data));
      })
  }





  return (
    <>

      <div className='maindiv'>
        Explore the options below according to your preference
</div>

      <div className='first'   >
        <div style={{ textDecoration: 'none' }}  onClick={()=> redirect(education.status?"educations":"/educations/register")} className='card1' >
          <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', flexDirection: 'column' }} >
            <div className='circle1'>
              <img src={educationIcon} />
            </div>
            <div onClick={getDetails} className='text' >
              EDUCATION
             </div>
          </div>
        </div>

        <div style={{ textDecoration: 'none' }} onClick={()=> redirect('/business')}  className='card2' >
          <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', flexDirection: 'column' }}>
            <div className='circle2'>
              <img src={businessIcon} />
            </div>
            <div className='text' >
              BUSINESS
             </div>
          </div>
        </div>
        <div style={{ textDecoration: 'none' }} onClick={()=> redirect('/eMagz')} className='card3' >
          <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', flexDirection: 'column' }} >
            <div className='circle3'>
              <img src={socialIcon} />
            </div>
            <div className='text' >
              eMagz
             </div>
          </div>

        </div    >
        <div style={{ textDecoration: 'none' }} onClick={()=> redirect('/video-conference')}  className='card4' >
          <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', flexDirection: 'column' }} >
            <div className='circle4'>
              <img src={videoIcon} />
            </div>
            <div className='text' >
              VIDEO CONFERENCING
             </div>
          </div>
        </div>
      </div>
      <div className='second' >

      </div>
    </>
  )

}

export default Viewboard;