import React, { useState, useEffect, useRef } from 'react';
import { Col, Form, FormGroup, Label, Input } from 'reactstrap';
import './index.css';
import Button from '@material-ui/core/Button';
import Badge from '@material-ui/core/Badge';
import Avatar from '@material-ui/core/Avatar';
import { makeStyles } from '@material-ui/core/styles';
import PhotoCamera from '@material-ui/icons/PhotoCamera';
import Header from '../Header';
import axios from 'axios';
import { BaseUrl, ImageUrl } from '../../../API'
import swal from 'sweetalert';
const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    '& > *': {
      margin: theme.spacing(1),
    },
  },
}));

export default function MyProfile() {
  const fileProfile = useRef("");
  const [user, setUser] = useState({});
  const [location, setLocation] = useState("");
  const [address1, setAddress1] = useState("");
  const [address2, setAddress2] = useState("");
  const [landmark, setLandmark] = useState("");
  const [city, setCity] = useState("");
  const [state, setState] = useState("");
  const [pincode, setPincode] = useState("");
  const [cityList, setCityList] = useState([]);
  const [stateList, setStateList] = useState([]);
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");

  useEffect(() => {
    let user = JSON.parse(localStorage.getItem("user"));
    setUser(user);
    setEmail(user.email == null ? "" : user.email);
    setName(user.name == null ? "" : user.name);
    setLocation(user.location == null ? "" : user.location);
    axios.get(`${BaseUrl}/states`).then(res => {
      if (res.data.status == true) {
        setStateList(res.data.data);
        axios.get(`${BaseUrl}/cities/${res.data.data[0]._id}`).then(res => {
          if (res.data.status == true) {
            setCity(res.data.data[0]._id)
            setCityList(res.data.data);
          }
        })
      }
    })
  }, [])

  const onUploadProfile = () => { fileProfile.current.click(); }
  const uploadProfile = event => {
    if (event.target.files.length > 0) {
      const formData = new FormData();
      formData.append("userimage", event.target.files[0], event.target.files[0].name);
      axios.post(`${BaseUrl}/user-profile-image/${user._id}`, formData).then(res => {
        if (res.data.status == true) {
          setUser(res.data.data);
          localStorage.setItem("user", JSON.stringify(res.data.data));
        }
      })
    }
  };
  const onUpdateProfile = () => {
    axios.post(`${BaseUrl}/edit-profile/${user._id}`, { name, location }).then(res => {
      if (res.data.status == true) {
        swal("Profile updated successfully.")
        let u = user;
        u.name = name;
        u.location = location;
        setUser(u);
        localStorage.setItem("user", JSON.stringify(u));
      }
    })
  };
  function setStateChange(state) {
    setState(state);
    axios.get(`${BaseUrl}/cities/${state}`).then(res => {
      if (res.data.status == true) {
        setCity(res.data.data[0]._id)
        setCityList(res.data.data);
      }
    })
  }
  const classes = useStyles();

  return (
    <>
      <Header title={"Profile"} />
      <div className='container container-top'>
        <div className='row rowView'>
          <Col sm={4}>
            <FormGroup >
              <Badge
                overlap="circle"
                anchorOrigin={{
                  vertical: 'bottom',
                  horizontal: 'right',
                }}
                badgeContent={<>
                  <input type='file' id='file' onChange={uploadProfile} ref={fileProfile} style={{ display: 'none' }} />
                  <PhotoCamera onClick={onUploadProfile} style={{ transform: 'translateX(90px)' }} />
                </>}
              >
                <Avatar style={{ transform: 'translateX(100px)',marginTop: -14, height:110, width:110}} alt={user.name} src={ImageUrl + user.image} />
              </Badge>
            </FormGroup>
            <FormGroup >
              <Input readOnly type="text" value={email} placeholder="Enter email" />
            </FormGroup>
            <FormGroup >
              <Input type="text" onChange={(e) => { setName(e.target.value) }} value={name} placeholder="Enter Name" />
            </FormGroup>
          </Col>
          <Col sm={4} className="text-center">
            <FormGroup >
              <select value={state} class="form-control" onChange={(e) => { setStateChange(e.target.value) }} >
                {stateList.map(item => {
                  return (<option value={item._id}>{item.name}</option>)
                })}
              </select>

            </FormGroup>
            <FormGroup >
              <select value={city} class="form-control" onChange={(e) => { setName(e.target.value) }} >
                {cityList.map(item => {
                  return (<option value={item._id}>{item.name}</option>)
                })}
              </select>

            </FormGroup>
            <FormGroup >
              <Input type="text" onChange={(e) => { setAddress1(e.target.value) }} value={address1} placeholder="Enter Address 1" />
            </FormGroup>
            <FormGroup >
              <Input type="text" onChange={(e) => { setAddress2(e.target.value) }} value={address2} placeholder="Enter Address 2" />
            </FormGroup>
          </Col>
          <Col sm={4} className="text-center">
           
            <FormGroup >
              <Input type="text" onChange={(e) => { setLocation(e.target.value) }}  value={location} placeholder="Enter Location" />
            </FormGroup>

            <FormGroup >
              <Input type="text" onChange={(e) => { setLandmark(e.target.value) }} value={landmark} placeholder="Enter Landmark" />
            </FormGroup>
            <FormGroup >
              <Input  type="text" onChange={(e) => { setPincode(e.target.value) }} value={pincode} placeholder="Enter Pincode" />
            </FormGroup>
            <Button className="ubtn" onClick={onUpdateProfile} variant="contained" size="large" >UPDATE</Button>
          </Col>
        </div>
      </div>
    </>
  );
}
