import React, { useState } from 'react';
import { Col, Form, FormGroup, Label, Input, FormText } from 'reactstrap';
import './index.css';
import Button from '@material-ui/core/Button';
import LockIcon from '@material-ui/icons/Lock';
import { Link } from '@material-ui/core';
import { useHistory } from "react-router-dom";
import Logo from '../../assets/Logo.png';
import * as loginUsers from '../ReduxStore/Actions/loginActions';
import { useDispatch, useSelector } from 'react-redux';
import { makeStyles } from '@material-ui/core/styles';
import Alert from '@material-ui/lab/Alert';
import axios from 'axios';
import { BaseUrl } from '../API';
import swal from 'sweetalert';



const Login = () => {
    const history = useHistory();
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const onHandleEmailChange = (e) => {
        setEmail(e.target.value)
    }
    const onHandlePasswordChange = (e) => {
        setPassword(e.target.value)
    }
    const submitDetails = (e) => {
        e.preventDefault();
        if (email === '' || password === '') {
            swal('Please fill All Details First!')
        } else {
            axios.post(`${BaseUrl}/login`, { email: email, password: password }).then(res => {
                if (res.data.status === true) {
                    localStorage.setItem("user", JSON.stringify(res.data.data))
                    redirect('/dashboard')
                } else {
                    swal("Please Enter Valid Details")
                }
            })
        }
    }
    const redirect = (url) => {
        history.push(url)
    }
    return (
        <>
            <div className='box ' >
                <div className='effect8'>
                    <div className='logincontainer'  >
                        <div style={{ marginTop: '27px', marginBottom: '20px' }} >
                            <div className='logoimg' >
                                <img className='loginImg' src={Logo} />
                            </div>
                        </div>
                        <div className='form' >
                            <div   >
                                <FormGroup >
                                    <Input onChange={onHandleEmailChange} value={email} style={{ height: '5vw' }} type="email" name="email" placeholder="Email" />
                                </FormGroup>
                                <FormGroup >
                                    <Input onChange={onHandlePasswordChange} value={password} style={{ height: '5vw' }} type="password" name="password" placeholder="Password" />
                                </FormGroup>
                                <Button onClick={submitDetails} style={{ backgroundColor: '#ED3237', color: 'white',  minWidth: "25vw", margin: "20px 0px" }} endIcon={<LockIcon>login</LockIcon>} variant="contained" size="large" color="primary" >
                                    LOGIN</Button>
                            </div>
                        </div>
                        <div className='forgetlink' >
                            <Link onClick={() => { redirect('/forget-password') }}  >Forget Password?</Link>
                        </div>
                        <div className="signup">
                            <Link onClick={() => { redirect('/register') }}  >I don't have account Sign up</Link>
                        </div>
                        <br />
                        <br />
                    </div>

                </div>

            </div>

        </>
    )


}


export default Login;


