import React, { useEffect, useState } from 'react';
import {BaseUrl,ImageUrl} from '../../../../API'
import axios from 'axios';
import {useSelector} from 'react-redux';
import { Button, Input, FormFeedback, FormText, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';
import Form from 'reactstrap/lib/Form';
 
let tp=0;
let tt=0;
let af=0;
// MSOP883W
const ApplyCoupon = ({history}) => {
  const [qty, setQty] = useState(1)
  const [totalPrice, setTotalPrice] = useState(0)
  const [afterPrice, setAfterPrice] = useState(0)
  const [totalTax, setTotalTex] = useState(0)
  const [total, setTotal] = useState(0)
  const [modal, setModal] = useState(false);
  const [open, setOpen] =  useState(false);
  const [coupon, setCoupon] = useState('')
  const [couponData, setCouponData] = useState('')
  const [reducePrice, setReducePrice] = useState(false)
  const [message, setMessage] = useState('')
  const [alertType, setAlertType] = useState('')
  const [error, setError] = useState('')
 
const userdata = JSON.parse(localStorage.getItem('user'))
const cartdata = useSelector(state=>state.cartReducer.cart);
 
console.log('Cart Data Coupon Component', cartdata)
const totalBugget = () =>{
  cartdata.map((item)=>{
    tp += Number(item.total_price);  
    tt += Number(item.gst_amount);
    console.log('TP, TT', item.total_price)
  })
  setTotalPrice(tp)
  setTotalTex(tt)
  {
    tp=0
    tt=0
    af=0
  }
}
 
  const applayCoupon =() =>{
    axios.get(`${BaseUrl}/view-coupon/${userdata._id}`).then(
      res=> {
        if(res.data.data.specificusercoupon.length<=0){
          setCouponData(res.data.data.anyusercoupon[0].coupon_value)
        }else{
          setCouponData(res.data.data.specificusercoupon[0].coupon_value)
        }
         
      }
  )
  }
  useEffect(()=>{
       applayCoupon() 
       if(cartdata){
        totalBugget()
       }
  }, [cartdata, totalBugget])
 
  const handlePlaceOrder = () =>{
    history.push('/business/payment-summery')
  }
  const toggle = () => setModal(!modal)
 
  function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
  }

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setOpen(false);
  };
  
  const couponHandler =()=>{
 
    if(coupon === 'MSOP883W'){
      setMessage('Coupon Applied Succeffully')
      setAlertType('success')
      af = totalPrice - couponData
      setAfterPrice(af)
      setReducePrice(true)
    }else{
      setAlertType('error')
      setModal(true)
      setMessage('Invalid Code!')
    }
    
  }
 
  const validateCoupon = (e) =>{
      const {target} = e
      if(target.value === ''){
        setError('has-danger')
      }else if(target.value.length <= 5 ){
          setError('has-danger')
      }else{
        setError('has-success')
      }
  }
  return (
    <>
      <div>
        {reducePrice ? <h5 style={{float: 'right'}}>Coupon applied</h5> : ( <p>{cartdata.length >0 ? <button className="apply-coupon" onClick={()=>{toggle()}}>Apply Coupon</button> : ''}</p>)}
        <p> <span>Total Products: {cartdata.length}</span></p>
         <p> <span>Total</span><span>M.R.P. :{ reducePrice ? Math.round(afterPrice) : Math.round(totalPrice)}</span></p>
        <p><span>Tax</span><span>Rs: {Math.round(totalTax)}</span></p>
        <Modal isOpen={modal} toggle={toggle}>
        <ModalHeader toggle={toggle}>Apply Coupon</ModalHeader>
        <ModalBody>
          <Form>
          <Input 
             placeholder="Please enter coupon here..." 
             value={coupon}
             valid={ error === 'has-success' }
             invalid={ error === 'has-danger' }
             onChange={(e)=>{setCoupon(e.target.value); validateCoupon(e)}} />
             <FormFeedback valid>
                That's a looking valid code you've got there.
              </FormFeedback>
              <FormFeedback>
                Uh oh! Looks like there is an issue with your code. Please input a correct code.
              </FormFeedback>
          </Form>

        </ModalBody>
        <ModalFooter>
          <Button style={{backgroundColor: "#ac3333"}} onClick={()=>{setOpen(true) ; error === 'has-success' ? toggle() : setModal(false) ;couponHandler()}}>Apply</Button>{' '}
          <Button style={{backgroundColor: "#ac3333"}} onClick={toggle}>Cancel</Button>
        </ModalFooter>
      </Modal>
      <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
        <Alert onClose={handleClose} severity={`${alertType}`}>
          {message}
        </Alert>
      </Snackbar>
      </div>
    </>
  )
}
 
 
export default ApplyCoupon;
