import React, { useEffect, useState } from 'react';
import './index.css';
import Header from '../Header';
import {BaseUrl,ImageUrl} from '../../../API'
import axios from 'axios';
import { useSelector , useDispatch} from 'react-redux';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSortDown } from '@fortawesome/free-solid-svg-icons';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { id } from 'date-fns/locale';

import { getCartData } from '../../../ReduxStore/Actions/CartActions';
import ApplyCoupon from "./ApplayCoupon"
const UserCarts = ({history}) => {
const userdata = JSON.parse(localStorage.getItem('user'))
// console.log('rr',userdata._id)
const dispatch = useDispatch();
const cartdata = useSelector(state=>state.cartReducer.cart);

  const [qty, setQty] = useState(1)
  const [index, setIndex] = useState(1)
  const [modal, setModal] = useState(false);

  const onNumberChange =(val)=>{
    let cartList=cartdata;
    cartList[index].quantity=val;
    dispatch(getCartData(cartList))
   setQty(val)
   toggle()
  }
  const removeQuantity=(id)=>{
    let cartList=cartdata;
    cartList.splice(id, 1);
    dispatch(getCartData(cartList))
  }
const changeQuantity=(item,id)=>{ 
  setIndex(id)
  setQty(item.quantity)
  toggle()
}
useEffect(() => {
  if (cartdata.length >= 0) {
      const body = {
          cart: cartdata,
          user_id: userdata._id
      }
      axios.post(`${BaseUrl}/cart-action`, body);
  }
}, [cartdata])
  const toggle = () => setModal(!modal);

  const handlePlaceOrder = () =>{
    history.push('/business/payment-summery')
  }
  return (
    <>
      <Header />
      <div className='container list_carts'>
      <div>
      <Modal isOpen={modal} toggle={toggle} >
        <ModalHeader toggle={toggle}>Select Quantity</ModalHeader>
        <ModalBody>   
          <div onClick={()=>onNumberChange('1')}  className='size_circle'>1</div>
          <div onClick={()=>onNumberChange('2')} className='size_circle'>2</div>
          <div onClick={()=>onNumberChange('3')} className='size_circle'>3</div>
          <div onClick={()=>onNumberChange('4')} className='size_circle'>4</div>
          <div onClick={()=>onNumberChange('5')} className='size_circle'>5</div>
        </ModalBody>
      </Modal>
    </div>
        <table>
        
          {cartdata.map((item,id)=>{
            return(
              <tbody>
            <tr>
              <td><img className="product" src={ImageUrl + item.picture} /></td>
            <td>{item.name}<br/>
            <button onClick={()=>changeQuantity(item,id)} className='qty'>Qty:{item.quantity} <FontAwesomeIcon style={{marginBottom:'2px'}} icon={faSortDown} />
             </button>
            </td>
            <td>Seller: {item.vendor_name}</td>
             <td>
                <td>{`${item.quantity} * ${item.total_price}`}</td>
               Amount Rs.{item.total_price * item.quantity} <small>({item.size})</small></td>
             <td><button onClick={()=>removeQuantity(id)} className='remove'>Remove</button> </td>
            </tr>
           
            </tbody>
            )
          })}
          
        </table>
        <ApplyCoupon />
       <div>
         {cartdata.length<= 0? <button disabled style={{cursor:'not-allowed',backgroundColor:'#bd5a5a'}} className="placeOrder">Order Now</button>
         :<button  onClick={handlePlaceOrder} className="placeOrder">Order Now</button>}
         </div>  
      </div>
      
    </>
  )
}


export default UserCarts;

