
import React, { useState } from 'react';
import { Row, Col, Form, FormGroup, Label, Input, FormText } from 'reactstrap';
import Button from '@material-ui/core/Button';
import { useHistory } from 'react-router-dom';
import Header from '../../Header';
import axios from 'axios';
import './index.css'
import swal from '@sweetalert/with-react';
import { BaseUrl } from '../../../API'




const CreateTest = () => {

  const [testid, setTestId] = useState('');

  const [testname, setTestName] = useState('');
  const [testtype, setTesttype] = useState('')
  const [duration, setDuration] = useState('');
  const [testdate, setTestdate] = useState('');
  const [subject, setSubject] = useState('');
  const [marks, setMarks] = useState('');
  const [classes, setClasses] = useState('');
  const [section, setSection] = useState('');
  const [qstniddata, setQuestioniddata] = useState([])
  const history = useHistory();

  const onHandleTestIdChange = (e) => {
    setTestId(e.target.value)
  }
  const onHandleTestNameChange = (e) => {
    setTestName(e.target.value)
  }
  const onHandleTestTypeChange = (e) => {
    setTesttype(e.target.value)
  }
  const onHandleDurationChange = (e) => {
    setDuration(e.target.value)
  }
  const onHandleTestDateChange = (e) => {
    setTestdate(e.target.value)
  }
  const onHandleTestSubjectChange = (e) => {
    setSubject(e.target.value)
  }
  const onHandleMarksChange = (e) => {
    setMarks(e.target.value)
  }
  const onHandleClassesChange = (e) => {

    setClasses(e.target.value)
  }

  const onHandleSectionChange = (e) => {

    setSection(e.target.value)
  }

  const submitDetails = () => {

    const data = {
      testid: testid,
      testname: testname,
      testtype: testtype,
      duration: duration,
      testdate: testdate,
      subject: subject,
      marks: marks,
      section: section,
      classes: classes

    }

    if (data.testdate === '' || data.testid === '' || data.testtype === '' || data.duration === '' || data.subject === "" || data.marks === '' || data.classes === '' || data.section === '') {
      swal(
        <div>
          <h1>Ooopss!</h1>
          <p>
            All fields are required field*
      </p>
        </div>
      )
    } else {
      axios.post(`${BaseUrl}/createnewtest`, data).then
        (res => {
          console.log('mm', res)

          if (res.status === 200) {
            swal(
              <div>
                <h1>Successfully Added!</h1>
                <p>
                  You Have Added Test Successfully!
              </p>
              </div>
            )
            history.push('/educations/teacher/test-list')
          } else {
            swal(
              <div>
                <h1>Ooopss!</h1>
                <p>
                  {res.msg}
                </p>
              </div>
            )
          }
        })
    }

    setSubject('');
    setTestId('')
    setTestName('')
    setMarks('')
    setTestdate('')
    setSubject('')
    setTesttype('')
    setDuration('')
    setClasses('')
    setSection('')
  }







  return (
    <>
      <Header />
      <div className='addstudent_container' >
        <div className='addstudent_card' >
          <Form>

            <Row form>
              <Col md={4}>

                <FormGroup>
                  <Label for="exampleEmail">Test Id*</Label>
                  <Input required value={testid} onChange={onHandleTestIdChange} type="text" placeholder="Enetr test id" />
                  <FormText color="muted">
                    This field is Required*
        </FormText>
                </FormGroup>
              </Col>
              <Col md={4}>
                <FormGroup>
                  <Label for="examplePassword">Test Name*</Label>
                  <Input required value={testname} onChange={onHandleTestNameChange} type="text" placeholder="Enetr test name" />
                  <FormText color="muted">
                    This field is Required*
        </FormText>
                </FormGroup>
              </Col>
              <Col md={4}>

                <FormGroup>
                  <Label for="exampleEmail">Test Types*</Label>
                  <Input required value={testtype} onChange={onHandleTestTypeChange} type="select"  >
                    <option>Select Test</option>
                    <option>Weekly Test</option>
                    <option>Surprise Test</option>

                  </Input>
                  <FormText color="muted">
                    This field is Required*
            </FormText>
                </FormGroup>
              </Col>
            </Row>

            <Row form>
              <Col md={4}>
                <FormGroup>
                  <Label for="exampleEmail">Duration(in hrs.)*</Label>
                  <Input required value={duration} onChange={onHandleDurationChange} type="text" name="city" placeholder="Enetr test city" />
                  <FormText color="muted">
                    This field is Required*
        </FormText>
                </FormGroup>
              </Col>
              <Col md={4}>
                <FormGroup>
                  <Label for="exampleState">Test Date*</Label>
                  <Input required value={testdate} onChange={onHandleTestDateChange}
                    type="date"
                    name="date"
                    id="exampleDate"
                    placeholder="date placeholder"
                  />
                  <FormText color="muted">
                    This field is Required*
        </FormText>
                </FormGroup>
              </Col>
              <Col md={4}>
                <FormGroup>
                  <Label for="exampleZip">Subject*</Label>
                  <Input required value={subject} onChange={onHandleTestSubjectChange} type="text" name="zip" placeholder="Enetr test subject" />
                  <FormText color="muted">
                    This field is Required*
        </FormText>
                </FormGroup>
              </Col>
            </Row>
            <Row form>
              <Col md={4}>
                <FormGroup>
                  <Label for="exampleCity">Marks*</Label>
                  <Input required value={marks} onChange={onHandleMarksChange} type="number" name="mark" placeholder="Enetr test mark" />
                  <FormText color="muted">
                    This field is Required*
        </FormText>
                </FormGroup>
              </Col>
              <Col md={4}>
                <FormGroup>
                  <Label for="exampleCity">Class*</Label>
                  <Input required value={classes} onChange={onHandleClassesChange} type="text" type="classes" placeholder="Enetr test Classes" />
                  <FormText color="muted">
                    This field is Required*
        </FormText>
                </FormGroup>
              </Col>
              <Col md={4}>
                <FormGroup>
                  <Label for="exampleCity">Section*</Label>
                  <Input required value={section} onChange={onHandleSectionChange} type="text" name="section" placeholder="Enetr test Section" />
                  <FormText color="muted">
                    This field is Required*
                 </FormText>
                </FormGroup>
              </Col>
            </Row>


            <Button onClick={submitDetails} variant="contained" color="primary"> Create Test</Button>
          </Form>
        </div>
      </div>
    </>
  )


}



export default CreateTest;













