import React, { useState } from 'react';
import { Form, FormGroup, Input } from 'reactstrap';
import './index.css';
import Button from '@material-ui/core/Button';
import LockIcon from '@material-ui/icons/Lock';
import FacebookIcon from '@material-ui/icons/Facebook';
import { Link } from '@material-ui/core';
import { useHistory } from 'react-router-dom';
import { useRouteMatch } from "react-router-dom";
import axios from 'axios';
import { BaseUrl } from '../API';
import Logo from '../../assets/Logo.png';
import swal from 'sweetalert';
const Register = () => {
  const [name, setUsername] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const match = useRouteMatch();
  const onHandleNameChange = (e) => {
    setUsername(e.target.value)
  }
  const onHandleEmailChange = (e) => {
    setEmail(e.target.value)
  }
  const onHandlePasswordChange = (e) => {
    setPassword(e.target.value)
  }
  const history = useHistory();
  const onHandleSubmit = (e) => {
    e.preventDefault();
    if(name.trim()==""){
      swal("Please enter name");
      return false;
    }else if(email.trim()==""){
      swal("Please Enter email");
      return false;
    }else if(password.trim()==""){
      swal("Please enter password");
      return false;
    }
    const data = {
      name: name,
      email: email,
      password: password
    }
    axios.post(`${BaseUrl}/register`, data).then
      (res => {
        console.log('reg', res.data.data);
        const newdata = JSON.stringify(res.data.data)
        const data = localStorage.setItem("user", newdata)
        if (res.data.status === true) {
          history.push('/dashboard');
        }else{
          swal(res.data.msg)
        }
      })
  }
  const redirectLogin = () => {
    history.push('/login')
  }
  return (
    <>
      <div className='box ' >
        <div className='effect8'>
          <div className='registercontainer'  >
            <div style={{ marginTop: '27px', marginBottom: '20px' }} >
              <div className='logoimg' >
                <img className='loginImg' src={Logo} />
              </div>
            </div>
            <div className='form' >
              <Form   style={{width:"50%"}}>
                <FormGroup >
                  <Input onChange={onHandleNameChange} value={name} name="name" placeholder="Name" />
                </FormGroup>
                <FormGroup >
                  <Input onChange={onHandleEmailChange} value={email} type="email" name="email" placeholder="Email" />
                </FormGroup>
                <FormGroup >
                  <Input onChange={onHandlePasswordChange} value={password} type="password" name="password" placeholder="Password" />
                </FormGroup>
                <Button onClick={onHandleSubmit} style={{ backgroundColor: '#ED3237', color: 'white',  width: "100%", margin: "20px 0px" }} endIcon={<LockIcon>login</LockIcon>} variant="contained" size="large" color="primary" >
                  REGISTER
                </Button>
                <h6>OR</h6>
                <Button style={{ margin: "20px 0px", width:"100%", color: '#ED3237',   border: '1px solid #ED3237', height: '40px', backgroundColor: 'white' }} startIcon={<FacebookIcon />} variant="outlined" color="primary">
                  Sign up with Facebook
                </Button>
                <br />
                <p className="signup">
                  <Link onClick={redirectLogin}  > I already have an account Sign in</Link>
                </p>
              </Form>
            </div>
            <br />
            <br />
          </div>
        </div>
      </div>
    </>
  )
}
export default Register;
