import React, { useEffect, useState } from 'react'
import './index.css';
import { useHistory } from 'react-router-dom';
import { BaseUrl, ImageUrl } from '../API'
import Header from '../Header';
import axios from 'axios';

const WelcomePage = () => {
    const [user, setUser] = useState({ name: "Guest User" });
    const history = useHistory();

const userdata = JSON.parse(localStorage.getItem('user'))

    useEffect(() => {
        let user = localStorage.getItem("user");
        if (user != null) {
            setUser(JSON.parse(user))
        }
    }, []);
    const changePage = (url) => {
        history.push(url)
    }

// const changeVendorFormPage=(url)=>{
//     history.push(url)
// }

const VendorPage=()=>{
axios.get(`${BaseUrl}/view-vendor/${userdata._id}`).then(
    res=>{
        console.log('nbnm',res)
        const data = JSON.stringify(res.data.data)
     localStorage.setItem("vendor", data)
        if(res.data.status === true){
            history.push('/business/vendor-dashboard/product-list')
        }else{
            history.push('/business/approve-form') 
        }  
    }
)
}



    return (
        <>
            <Header />
            <div className='welcomeHead'  >
                <div className='welcomecard' >
                    <div className='avatarImg' >
                        <img alt={user.name} src={user.image || 'http://api.emagz.live/assets/profile/avatar-icon.png'} className='avtprof' />
                    </div>
                    <div className=' WELCOME_BACK' >WELCOME</div>
                    <div className='profname' >{user.name}</div>
                    <div className='vendorbuttons' >
                        <button onClick={() => changePage('/business/user-dashboard')} className='vendorbtn vendorbtn1' >CONTINUE AS USER</button>
                    </div>
                    <div className='vendorbuttons' >
                        <button onClick={VendorPage} className='vendorbtn vendorbtn1' >CONTINUE AS VENDOR</button>
                    </div>

                    {/* <div className='switchbuttons' >
                        <button className='switchbtn switchbtn1' >SWITCH ACCOUNT</button>
                    </div> */}

                </div>
            </div>
        </>
    )
}
export default WelcomePage;














