import * as types from '../../types';



const initialState={
    product_id:'',
    product_detail_id:'',
    quantity:'',
    total_price:'',
    price:'',
    gst_amount:'',
    cart: []
  }

  export const cartReducer =(state=initialState,action)=>{
    switch(action.type){               
        case types.ADD_TO_CART:
          
            return {
                ...state, 
               cart:[...action.cartdata]
            }
           
            default:
                return state;
    }
}










