import React, { useState, useEffect } from 'react';
import './index.css';
import Header from '../Header';
import Avatar from '@material-ui/core/Avatar';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import IconButton from '@material-ui/core/IconButton';
import svgIcon from '../../../../assets/Flat.svg';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import ProductCard from './ProductCard';
import Axios from 'axios';
import { BaseUrl, ImageUrl } from '../../../API';
// import ProductImage from '../../../../../assets/product.png';
import { useHistory } from 'react-router-dom';
import Pagination from '@material-ui/lab/Pagination';

function TabPanel(props) {
  const { children, value, index, ...other } = props;
  return (
    <div>
      {value === index && (
        <Box p={3}>
          <div>{children}</div>
        </Box>
      )}
    </div>
  );
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    width: '100%',
    backgroundColor: theme.palette.background.paper,
  },
}));
const UserDashboard = () => {
  const classes = useStyles();
  const [value, setValue] = useState(0);
  const handleChange = (event, newValue) => {
    console.log('oo', newValue)
    setValue(newValue);
  };
  const [activeStep, setActiveStep] = useState(0);
  const [catimage, setCatimage] = useState([]);
  const [subcat, setSubcat] = useState([]);
  const [tabID, setTabID] = useState(0);
  const [Category, setCategory] = useState('');
  const [productDetailslist, setProductdetailslist] = useState([]);
  const [catproduct, setCatproduct] = useState([]);

  const [names, setName] = useState([
    { name: 'Debanjan' },
    { name: 'Arjun' },
    { name: 'Nasim' },
    { name: 'Sudipta' },
    { name: 'Ayesh' },
    { name: 'Debasish' },
    { name: 'Raja' },
    { name: 'Snehasish' },
    { name: 'Ayesh' },
    { name: 'Debasish' },
    { name: 'Raja' },
    { name: 'Snehasish' },
    { name: 'Ayesh' },
    { name: 'Debasish' },
    { name: 'Raja' },
    { name: 'Snehasish' },
    { name: 'Ayesh' },
    { name: 'Debasish' },
    { name: 'Raja' },
    { name: 'S' },
  ]);

  // console.log('xx',value)

  const maxNames = names.length;
  const width = window.innerWidth + 20;
  const length = names.length * 100;
  const history = useHistory();
  const handleNext = () => {
    activeStep === 0 ? setActiveStep(-200 * (maxNames - 1)) : setActiveStep(activeStep + 200);
    setActiveStep(activeStep + 200);
  };

  const handleBack = () => {
    if (width - activeStep < length) {
      activeStep === -200 * (maxNames - 1) ? setActiveStep(0) : setActiveStep(activeStep - 200);
      setActiveStep(activeStep - 200);
    }
  };

const ProductDetails=(index,products)=>{
        history.push("product-details",{products,index})
}

  useEffect(() => {
    getAllCategory();
  }, [])
  const getAllCategory = () => {
    Axios.get(`${BaseUrl}/view-business-category`).then(
      res => {
        let response = res.data.data;
        setCatimage(response);
        setCategory(response[0]);
        getTabdata(response[0],0)
        setSubcat(response[0].subcategories)

      }
    )

  }
  const getTabdata = (value, id) => {
    // console.log('mm', id)
    setTabID(id)
    if (id ===0) {
      Axios.get(`${BaseUrl}/product-list-category/${value._id}`).then(
        res => {
          setCatproduct(res.data);
        }
      )
    } else {
      Axios.get(`${BaseUrl}/product-list-sub-category/${value._id}`).then(
        res => {
          setCatproduct(res.data);
        }
      )
    }


  }

  const getCategoryId = (category) => {
    setCategory(category);
    setSubcat(category.subcategories)
  }









  return (
    <>
      <Header />
      <div className='main_container'  >
        <div className='userdash_container' >
          <div className='userdash_main' >
            {catimage.map(((val, index) => {
              return (
                <div key={val._id} style={{ transform: `translateX(${activeStep}%)`, transition: '0.5s' }} >
                  <div onClick={() => {
                    getCategoryId(val)
                  }} className='items_card' >
                    <img src={ImageUrl + val.picture} />
                  </div>

                </div>

              )
            }))}
            <div style={{ position: 'absolute', left: 0, zIndex: 100, paddingLeft: '10px' }}>
              {activeStep === 0 ? null : <IconButton onClick={handleNext} style={{ outline: 'none' }}  >
                <ChevronLeftIcon style={{ backgroundColor: '#f05458c2', borderRadius: '50px', marginTop: '3px', outline: 'none' }} />
              </IconButton>}
            </div>
            <div style={{ right: 0, position: 'absolute', zIndex: 100, paddingRight: '10px' }} >
              {width - activeStep > length ? null : <IconButton style={{ outline: 'none' }} onClick={handleBack} >
                <ChevronRightIcon style={{ backgroundColor: '#f05458c2', borderRadius: '50px', marginTop: '3px', outline: 'none' }} />
              </IconButton>}
            </div>



          </div>
          <div className='user_body' >
            <div className={classes.root}>
              <AppBar position="static" color="default">

                <Tabs
                  value={tabID}
                  onChange={handleChange}
                  indicatorColor="primary"
                  textColor="primary"
                  variant="scrollable"
                  scrollButtons="auto"
                  aria-label="scrollable auto tabs example"
                >
                  <div key={0}>
                    <Tab label={Category.name + " >>"} onClick={() => {
                      getTabdata(Category, 0)
                    }} />
                  </div>
                  {subcat.map((item, index) => {
                    return (
                      <div key={item._id}>
                        <Tab label={item.name} onClick={() => {
                          getTabdata(item, index+1)
                        }} />
                      </div>
                    )
                  })}
                </Tabs>
              </AppBar>
              {catproduct.status === true ? <div className='main_userBoard_container'>
                {catproduct.data.map((products, id) => {
                  console.log("products",products)
                  return ( 
                      <>
                        {products.productdetail.map((product,index) => {
                          product.images.length >0 && console.log("product Images RIYAZ",product.images[0].picture)
                          return (
                            <div className='product_card_container'>
                                  <div className='product_card' onClick={() => ProductDetails(index,products)} >
                                    <div className='product_card_head'>{products.name}</div>
                                    <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center' }} >
                                      <div className='product_cost' > Rs.{product.price}</div>
                                      <div className='product_weight'>{product.size}</div>
                                    </div>
                                    <div key={product._id} className='image_container' >
                                      { product.images.length >0 && <img style={{ marginTop: '0px' }} className='products_image' src={ImageUrl + product.images[0].picture} /> } 
                                    </div>
                                  </div>
                            </div>
                          )
                        })}
                      </>
                  )
                })}
              </div> : <div>
                  {productDetailslist.map((products, id) => {
                    console.log('pro',products);
                    return (
                      <TabPanel value={tabID} index={id + 1} >

                        
                          {products.productdetail.map(product => {
                           
                            return (
                              <div className='product_card_container'>
                              <div className='product_card' onClick={() => ProductDetails(product._id,"product-details")} >
                                <div className='product_card_head'>{products.name}</div>
                                <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center' }} >
                                  <div className='product_cost' > Rs.{product.price}</div>
                                  <div className='product_weight'>{product.size}</div>
                                </div>
                                <div key={product._id} className='image_container' >
                                   <img style={{ marginTop: '0px' }} className='products_image' src={ImageUrl + product.images[0].picture} /> 
                                </div>
                              </div>
                        </div>

                            )
                          })}
                      </TabPanel>
                    )
                  })}

                </div>}


            </div>
          </div>
        </div>
        <div className='user_pagination'>
          <Pagination count={10} color="secondary" />
        </div>

      </div>

    </>
  )


}


export default UserDashboard;













